body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.everything {
  width: 100vw;
  height:100vh;
  background-color: black;
  color: #FFD700;
  text-align: center;
}

.title {
  font-size: 100px;
}

.question {
  position: relative;
  top:40vh;
  font-size: 30px;
}

.input_field {
  background-color: black;
  border: none;
  width:25%;
  color: #FFD700;
  position: relative;
  top:50vh;

  border: 1px solid transparent;
  border-top: none;
  padding: 15px;
  -webkit-border-image: url(/sw_api/static/media/border.7f0385fb.png) 30 stretch; /* Safari 3.1-5 */


}

::-webkit-input-placeholder {
  color: #FFD700;
}

::-ms-input-placeholder {
  color: #FFD700;
}

::placeholder {
  color: #FFD700;
}
